.page {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
  color: #444444;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.header {
  min-height: 79px;
  position: relative;
}
@media (min-width: 768px) {
  .header {
    min-height: 89px;
  }
}

.header__logo-wrapper {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  /* min-height: 79px; */
  /*   background-color: bisque; */
}
@media (max-width: 767px) {
  .header__logo-wrapper {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
@media (min-width: 768px) {
  .header__logo-wrapper {
    top: 22px;
    left: 50px;
    z-index: 1;
  }
}
@media (min-width: 1150px) {
  .header__logo-wrapper {
    top: 9px;
    left: 50%;
    transform: translate(-50%);
  }
}

.header__logo {
  cursor: pointer;
  display: flex;
  width: 86px;
  height: 35px;
  background-image: url("/img/mishka-logo-mobile.svg");
  background-position: center;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .header__logo {
    width: 113px;
    height: 46px;
    background-image: url("/img/mishka-logo-tablet.svg");
  }
}
@media (min-width: 1150px) {
  .header__logo {
    width: 149px;
    height: 72px;
    background-image: url("/img/mishka-logo-desktop.svg");
  }
}

.header__logo-svg {
  width: 86px;
  height: 35px;
}

.footer {
  margin-top: auto;
  background: #F1F1F1;
}

.footer__wrapper {
  display: flex;
  flex-direction: column;
  min-height: 160px;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .footer__wrapper {
    flex-direction: row;
    align-items: center;
    min-height: 143px;
  }
}
@media (max-width: 767px) {
  .footer__logo-link {
    display: none;
  }
}

.footer__social-list {
  display: flex;
  justify-content: center;
}
@media (max-width: 767px) {
  .footer__social-list {
    margin-bottom: 38px;
    position: relative;
  }
  .footer__social-list::after {
    content: "";
    position: absolute;
    bottom: -18px;
    left: auto;
    right: auto;
    width: calc(100vw - 60px);
    height: 1px;
    background-color: #E5E5E5;
  }
}
@media (min-width: 768px) {
  .footer__social-list {
    margin-left: auto;
    margin-right: auto;
  }
}

.footer__social-item:not(:last-child) {
  margin-right: 40px;
}

.footer__copyright {
  display: flex;
  color: inherit;
}

.footer__copyright-link {
  font-weight: 700;
  font-size: 19px;
  line-height: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: inherit;
}
.footer__copyright-link:visited {
  color: inherit;
  text-decoration: inherit;
}

.footer__copyright-image {
  margin-left: 27px;
  width: 27;
  height: 34;
}

.footer__logo-link,
.footer__social-link,
.footer__copyright-link {
  outline: none;
}

.footer-button {
  outline: none;
  fill: #231F20;
}
.footer-button.footer-button__accent-color {
  fill: #63D1BB;
}
.footer-button:hover, .footer-button:focus {
  fill: #46C1AE;
}
.footer-button:active {
  fill: #46C1AE;
  opacity: 0.3;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  width: 100%;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  color: #444444;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  min-height: 52px;
  background-color: #FFFFFF;
  border: 2px solid #444444;
  cursor: pointer;
  padding-left: 42px;
  padding-right: 42px;
  margin-left: auto;
  margin-right: auto;
}
.button:hover, .button:focus {
  color: #FFFFFF;
  background-color: #444444;
  border-color: transparent;
  outline: none;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #444444;
  border-color: transparent;
  outline: none;
}

.button--secondary {
  color: #FFFFFF;
  background-color: #63D1BB;
  border-color: transparent;
}
.button--secondary:hover, .button--secondary:focus {
  color: #FFFFFF;
  border-color: transparent;
  outline: none;
}
.button--secondary:active {
  color: rgba(255, 255, 255, 0.3);
  border-color: transparent;
  outline: none;
}

