.control-checkbox {
  position: relative;
  display: block;
  padding-left: 61px;
  cursor: pointer;
  height: 100%;
}
@media (min-width: 768px) {
  .control-checkbox {
    padding-left: 82px;
  }
}

.control-checkbox__mark {
  position: absolute;
  top: 0;
  left: 0;
  width: 38px;
  height: 38px;
  border: 2px solid #444444;
}
@media (min-width: 768px) {
  .control-checkbox__mark {
    width: 52px;
    height: 52px;
  }
}

.control-checkbox__label {
  display: flex;
  align-items: center;
  height: 100%;
}

.control-checkbox__input:checked + .control-checkbox__mark::before {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  content: "";
  background-image: url("../img/checkbox-tick-mobile.svg");
  background-position: center;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .control-checkbox__input:checked + .control-checkbox__mark::before {
    background-image: url("../img/checkbox-tick-tablet.svg");
  }
}

.control-radio {
  position: relative;
  display: block;
  padding-left: 61px;
  cursor: pointer;
  height: 100%;
}
@media (min-width: 768px) {
  .control-radio {
    padding-left: 82px;
  }
}

.control-radio__mark {
  position: absolute;
  top: 0;
  left: 0;
  width: 42px;
  height: 42px;
  border: 2px solid #444444;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .control-radio__mark {
    width: 52px;
    height: 52px;
  }
}

.control-radio__label {
  display: flex;
  align-items: center;
  height: 100%;
}

.control-radio__input:checked + .control-radio__mark::before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  content: "";
  width: 18px;
  height: 18px;
  background-color: #62D1BA;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .control-radio__input:checked + .control-radio__mark::before {
    width: 23px;
    height: 23px;
  }
}

